import React, { useEffect, useState } from 'react';
import { Button, Dropdown, Nav, Navbar } from 'react-bootstrap';
import SearchBarContainer from '../../containers/SearchBarContainer';
import icons from '../../images/icons';
import '../../styles/client/components/TopMenu.scss';
import classNames from 'classnames';
import ModalBox from './ModalBox';
import LoginForm from './SignInOutForm';

export default function TopMenu({ ...props }) {
  const [isHide, setHide] = useState(false);
  const [isVisible, setVisible] = useState(false);
  const [isSearchMobile, setSearchMobile] = useState(false);
  const [isMobile, setMobile] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const { isHome } = props;

  useEffect(() => {
    if (isHome) {
      const hideMenu = () => {
        if (window.pageYOffset === 0 || window.pageYOffset > 500) {
          return setHide(false);
        } else {
          return setHide(true);
        }
      };
      const hideSearch = () => {
        if (window.pageYOffset > 500) {
          return setVisible(true);
        } else {
          return setVisible(false);
        }
      };
      window.addEventListener('scroll', hideMenu, true);
      window.addEventListener('scroll', hideSearch, true);
      return () => {
        window.removeEventListener('scroll', hideMenu, true);
        window.removeEventListener('scroll', hideSearch, true);
      };
    }
  }, [isHome]);

  const link =
    'data:image/svg+xml;base64,';

  if (isSearchMobile && isHome)
    return (
      <div className='search-mobile'>
        <div className='header'>
          <SearchBarContainer isTop={true} setSearchMobile={setSearchMobile} />
          <div className='btn-back' onClick={() => setSearchMobile(false)}>
            <i className='fa fa-arrow-right' aria-hidden='true'></i>
          </div>
        </div>
      </div>
    );

  return (
    <>
      <Navbar
        className={
          isHome &&
          classNames('is-home', { hide: isHide, isVisible: isVisible })
        }
        expand='lg'
        fixed='top'>
        <div
          className='hamburger-icon d-lg-none'
          onClick={() => setMobile(!isMobile)}>
          <span></span>
          <span></span>
          <span></span>
        </div>

        <Navbar.Brand className='pl-3 mr-auto' href='/'>
          <img className='img-fluid' src={link} alt='logo' />
        </Navbar.Brand>

        {isHome && (
          <div className='search-bar-container d-lg-block d-none'>
            <SearchBarContainer isTop={true} />
          </div>
        )}

        <Nav className='ml-auto mr-3 flex-row d-none d-lg-flex'>
          <Nav.Link href='#home'>Offer</Nav.Link>
          <Nav.Link href='#link'>
            <i className='fa fa-question-circle mr-2' aria-hidden='true'></i>
            Need Help?
          </Nav.Link>
          <Dropdown>
            <Dropdown.Toggle variant='success' id='dropdown-menu-align-right'>
              <div className='d-flex align-items-center'>
                <img className='icon mr-2' src={icons.English} alt='english' />{' '}
                English
              </div>
            </Dropdown.Toggle>

            <Dropdown.Menu alignRight={true} className='p-0'>
              <Dropdown.Item href='#/action-1'>
                <img
                  className='dropdown-icon'
                  src={icons.English}
                  alt='english'
                />
                English
              </Dropdown.Item>
              <Dropdown.Item href='#/action-2'>
                <img
                  className='dropdown-icon'
                  src={icons.German}
                  alt='german'
                />
                German
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
          <Button
            onClick={() => setShowModal(true)}
            className='btn-join ml-3'
            type='button'>
            Join
          </Button>
        </Nav>
        {isHome && (
          <div className='d-lg-none px-2' onClick={() => setSearchMobile(true)}>
            <i className='fa fa-search' aria-hidden='true'></i>
          </div>
        )}
        <div className={classNames('navbar-mobile', { show: isMobile })}></div>
      </Navbar>
      {showModal && (
        <ModalBox show={showModal} setShow={setShowModal}>
          <LoginForm />
        </ModalBox>
      )}
    </>
  );
}
