import React, { useEffect, useRef, useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import Banner from "../../components/client/Banner";
import Offer from "../../components/client/Offer";
import Sidebar from "../../components/client/Sidebar";
import TopMenu from "../../components/client/TopMenu";
import ProductsContainer from "../../containers/ProductsContainer";
import CartContainer from "../../containers/CartContainer";
import ModalBox from "../../components/client/ModalBox";

export default function Home({ ...props }) {
  const productsRef = useRef(null);
  const [show, setShow] = useState(true);

  useEffect(() => {
    window.scrollTo({
      top: productsRef.current.offsetTop - 100,
      behavior: "smooth",
    });
  }, []);

  return (
    <>
      <TopMenu isHome={true} />
      <Banner />
      <Container className='p-0' fluid>
        <Offer />
        <Row className='m-0'>
          <Col xl={2} lg={3} className='p-0'>
            <Sidebar myRef={productsRef} />
          </Col>
          <Col ref={productsRef} xl={10} lg={9} className='p-0'>
            <ProductsContainer history={props.history} />
          </Col>
        </Row>
      </Container>
      <CartContainer />

      <ModalBox show={show} setShow={setShow}>
        <div className='text-center'>
          <h1 className='text center'> Thng tin nhm 3:</h1>
          <h4>Nguyn Quc Vit - 18521643</h4>
          <h4>Trng Tho Tin - 18521495</h4>
          <h4>H Vn Anh T - 18521580</h4>
          <h4>Nguyn Hu Vinh - 18521651</h4>
        </div>
      </ModalBox>
    </>
  );
}
